import './App.css';
import logo from "./assets/img/logo.svg";
import wave from "./assets/img/wave.svg";
import surfingboard from "./assets/img/surfingboard.svg"
import surfingboard2 from "./assets/img/surfingboard2.svg"
import stone from "./assets/img/stone.svg"
import tree from "./assets/img/tree.svg"
import { BrowserRouter, Link, Switch } from "react-router-dom";

import {
  Routes,
  Route,
} from "react-router-dom";
import DateSelect from "./pages/dateSelect"
import Cafe from "./pages/Cafe"
import CarLod from "./pages/CarLod"
import Airplane from "./pages/Airplane"
import Food from "./pages/Food"
import Tour from "./pages/Tour"
import Result from "./pages/Result"


function Home() {
  return (
    <div className="App">
      <div>
        <img src={logo} className="logo" alt="" />
        <h1 className="mainTitleLogo"> </h1>
      </div>
      <div>
        <Link to="/type">
          <div className="button">
            <button className="mainButton">   </button>
          </div>
        </Link>
        <Link to="/tour">
          <div className="button">
            <button className="mainButton2"> </button>
          </div>
        </Link>
      </div>
      <img src={wave} className="wave" alt="" />
      <img src={surfingboard2} className="surfingboard2" alt="2" />
      <img src={surfingboard} className="surfingboard" alt="" />
      <img src={stone} className="stone" alt="" />
      <img src={tree} className="tree" alt="" />
    </div>
  );
}

function App() {

  return (
    <BrowserRouter basename="/prejeju" forceRefresh={true} >
      <Switch >
        <Route exact path="/">
          <Home />
        </Route>
        <Route path="/type">
          <DateSelect />
        </Route>
        <Route path="/cafe" >
          <Cafe />
        </Route>
        <Route path="/airplane">
          <Airplane />
        </Route>
        <Route path="/tour" >
          <Tour />
        </Route>
        <Route path="/result" >
          <Result />
        </Route>

      </Switch>
    </BrowserRouter>
  );
}

export default App;
